import React, { useState } from "react";
import { Link } from "react-router-dom";
import userDB from "../data/db";

export default () => {
  const [userName, setUserName] = useState("");
  const [password, setPassword] = useState("");
  const [firstName, setFirstName] = useState("");
  const [alert, setAlert] = useState({ message: "", type: "" });

  const handleSubmit = e => {
    e.preventDefault();
    if (userName && password && firstName) {
      userDB.push({
        userName,
        firstName,
        password
      });

      sessionStorage.setItem("userDB", JSON.stringify(userDB));
      setAlert({
        message: "Account Created SuccessFully, You can now login",
        type: "success"
      });
      setFirstName("");
      setPassword("");
      setUserName("");
      return;
    } else {
      setAlert({ message: "Please fill all fields", type: "error" });
      return;
    }
  };
  return (
    <>
      <h2>Create new Account</h2>
      {alert.message && <h5 className={alert.type}>{alert.message}</h5>}
      <div className="register">
        <form onSubmit={e => handleSubmit(e)}>
          <p>
            First Name:
            <input
              type="text"
              name="firstName"
              placeholder="your name here"
              value={firstName}
              onChange={e => setFirstName(e.currentTarget.value)}
              autoComplete="true"
            />
          </p>
          <p>
            Username:
            <input
              type="text"
              name="userName"
              placeholder="type username"
              value={userName}
              onChange={e => setUserName(e.currentTarget.value)}
              autoComplete="true"
            />
          </p>
          <p>
            Password:
            <input
              type="password"
              placeholder="type password"
              value={password}
              onChange={e => setPassword(e.currentTarget.value)}
              name="password"
              autoComplete="true"
            />
          </p>

          <button>Create Account</button>
        </form>
        <p className="App-link">
          Already have Account ? <Link to="/login-react">Login here</Link>
        </p>
      </div>
    </>
  );
};
