import React from "react";
import { Link } from "react-router-dom";

const Login = ({ dispatch, userName, password }) => {
  const handleSubmit = e => {
    e.preventDefault();
    dispatch({ type: "login" });
  };

  return (
    <div className="login">
      <h2>Please login</h2>
      <form onSubmit={e => handleSubmit(e)}>
        <input
          type="text"
          name="userName"
          autoComplete="true"
          placeholder="username"
          value={userName}
          onChange={e =>
            dispatch({
              type: "input",
              userName: e.currentTarget.value,
              field: "userName"
            })
          }
        />
        <input
          type="password"
          name="password"
          autoComplete="true"
          placeholder="password"
          value={password}
          onChange={e =>
            dispatch({
              type: "input",
              password: e.currentTarget.value,
              field: "password"
            })
          }
        />
        <button>Login</button>
      </form>
      <p className="App-link">
        You don't have Account{" "}
        <Link to="/login-react/register">Create Now</Link>
      </p>

      <pre>
        for Testing login with: <br />
        user1 1234
      </pre>
    </div>
  );
};

export default Login;
