import React, { useReducer } from "react";
import "./App.css";
import Login from "./components/login";
import Profile from "./components/profile";
import userDB from "./data/db";

const db = localStorage.getItem("userDB")
  ? JSON.parse(sessionStorage.getItem("userDB"))
  : userDB;

const verifyLogin = (user, pass, db) => {
  const result = db.filter(
    ({ userName, password }) => userName === user && pass === password
  );

  return result;
};

const loginReducer = (state, action) => {
  switch (action.type) {
    case "login":
      const user = verifyLogin(state.userName, state.password, db);
      if (user.length) {
        return {
          ...state,
          isLogedIn: true,
          firstName: user[0].firstName,
          message: "Successfully logged in"
        };
      }
      return {
        ...state,
        message: "Incorrect Username or Password"
      };
    case "logout":
      return {
        ...state,
        isLogedIn: false,
        userName: "",
        password: "",
        message: "Successfully logged out"
      };
    case "input":
      return {
        ...state,
        [action.field]: action[action.field]
      };
    default:
      return state;
  }
};

const instialState = {
  isLogedIn: false,
  userName: "",
  password: "",
  firstName: "",
  error: ""
};

function App() {
  const [state, dispatch] = useReducer(loginReducer, instialState);

  return (
    <>
      {state.message && (
        <p className={state.isLogedIn ? "success" : "error"}>
          {state.message}{" "}
        </p>
      )}

      {state.isLogedIn ? (
        <Profile dispatch={dispatch} {...state} />
      ) : (
        <Login dispatch={dispatch} />
      )}
    </>
  );
}

export default App;
