import React from "react";

const Profile = ({ userName, firstName, dispatch }) => {
  return (
    <div>
      <h1>
        Welcome <small>{firstName}</small>
      </h1>
      <h3>You are Loged in as {userName}</h3>
      <button onClick={() => dispatch({ type: "logout" })} className="logout">
        Logout
      </button>
    </div>
  );
};

export default Profile;
